.IntroContainer{
    border: gray 1px inset;
    /* background-color: rgba(0, 0, 0, 0.5); */
    /* backdrop-filter: blur(5px); */
    /* box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5); */
    margin: 50px;
    height: calc(100vh - 100px);
    background: 
        linear-gradient(140deg, rgba(255, 255, 255, 0), rgb(0, 0, 0));

    /* filter: invert(25%); */
}

.DavidHess{
    font-size: 60px;
    font-family: 'Coolvetica';
    text-align: left;
    color: rgb(23, 23, 23);
    margin-inline: 25px;
    /* border: black 1px solid; */
}

.Subtitle{
    font-size: 20px;
    font-family: 'Coolvetica';
    text-align: left;
    color: rgb(23, 23, 23);
    margin-inline: 25px;
    /* border: black 1px solid; */

}

.IntroLinks{
    font-size: 25px;
    font-family: 'Coolvetica';
    text-align: left;
    color: rgb(23, 23, 23);
    padding-top: 25px;
}

.IntroLink{
    margin-inline: 25px;
    margin-top: 25px;
    font-weight: bold;
    border: black 1px solid;
    transition: transform 0.3s ease; /* Smooth transition */
}

.IntroLink:hover{
    transform: translate(10px);
}

.IntroBlurb{
    color:gray;
    font-size: 25px;
    font-family: 'Coolvetica';
    text-align: left;
    position: absolute;
    right: 75px;
    bottom: 75px;
    /* border: green solid 1px; */
    width: 200px;
}
